canvas {
  width: 100%;
  height: 100%; }

.title {
  padding: 0;
  margin: 0;
  color: #ae12d4; }

button {
  background-color: transparent;
  border: 0;
  cursor: pointer; }

.play-intro {
  position: absolute;
  top: 50%;
  left: 50%;
  display: none;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 100px;
  width: 100px;
  cursor: pointer; }

.play-intro svg {
  width: 100%; }

