  }
  */
.control-show {
  display: block; }

.loader {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 0;
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  -webkit-transform-origin: left;
          transform-origin: left;
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  transition: -webkit-transform 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  transition: transform 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  transition: transform 0.3s cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  background: -webkit-gradient(linear, left top, right top, from(#16fd03), to(#00ff8a));
  background: linear-gradient(to right, #16fd03, #00ff8a); }

a {
  color: inherit;
  text-decoration: none; }

.credits {
  margin: 20px;
  position: absolute;
  z-index: 1;
  bottom: 10px;
  width: 70%; }

.credits h1 {
  font-size: 20pt;
  width: 80%;
  line-height: 1.4; }

.credits .controls svg {
  width: 5vw; }

.removeLoader {
  -webkit-transition: -webkit-transform 0.3s 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  transition: -webkit-transform 0.3s 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  transition: transform 0.3s 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  transition: transform 0.3s 0.3s cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 0.3s 0.3s cubic-bezier(0.23, 1, 0.32, 1); }

@media screen and (min-width: 768px) {
  .credits {
    margin-left: 20px;
    margin-top: 20px;
    position: absolute;
    z-index: 1;
    bottom: inherit;
    width: 60%; }
  .credits h1 {
    font-size: 20pt;
    width: auto;
    line-height: 1.4; }
  .credits .controls {
    right: 50px; }
  .credits .controls svg {
    width: auto; } }

