.Audio3D{
    width: 70%;
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Ropa Sans', sans-serif;
    background-color: #fff700;
    color: #ae12d4;
    box-sizing: border-box;
    overflow: hidden;
    cursor: -webkit-grab;
    cursor: -moz-grab;
    text-align: center;
    & h1{
      margin-top: 10vh;
      font-size: 35px;
      color: #ae12d4;
      padding: 0 10%;
    }
    & h2{
      color: #ae12d4;
      padding: 0 10%;
    }
}


canvas {
    width: 100%;
    height: 100%;
  }
  
  .title {
    padding: 0;
    margin: 0;
    color: #ae12d4;
  }
  
  button {
    background-color: transparent;
    border: 0;
    cursor: pointer;
  }
  
  .play-intro {
    position: absolute;
    top: 50%;
    left: 50%;
    display: none;
    transform: translate(-50%, -50%);
    height: 100px;
    width: 100px;
    cursor: pointer;
  }
  .play-intro svg {
    width: 100%;
  }
  
  /*
  .controls {
    position: absolute;
    right: 30px;
    top: 10px;
  }
  
  .play, .pause {
    display: none;
  }
  */
  
  .control-show {
    display: block;
  }
  
  .loader {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    z-index: 0;
    transform: scale(0, 1);
    transform-origin: left;
    transition: transform 0.3s cubic-bezier(0.23, 1, 0.32, 1);
    background: linear-gradient(to right, #16fd03, #00ff8a);
  }
  
  a {
    color: inherit;
    text-decoration: none;
  }
  
  .credits {
    margin: 20px;
    position: absolute;
    z-index: 1;
    bottom: 10px;
    width: 70%;
  }
  .credits h1 {
    font-size: 20pt;
    width: 80%;
    line-height: 1.4;
  }
  .credits .controls svg {
    width: 5vw;
  }
  
  .removeLoader {
    transition: transform 0.3s 0.3s cubic-bezier(0.23, 1, 0.32, 1);
  }
  
  @media screen and (min-width: 768px) {
    .credits {
      margin-left: 20px;
      margin-top: 20px;
      position: absolute;
      z-index: 1;
      bottom: inherit;
      width: 60%;
    }
    .credits h1 {
      font-size: 20pt;
      width: auto;
      line-height: 1.4;
    }
    .credits .controls {
      right: 50px;
    }
    .credits .controls svg {
      width: auto;
    }
  }